.item {
    height: 200px;
    padding: 2px;
    box-sizing: border-box;
    font-size: 0;
    color: rgba(0, 0, 0, 0);
}
.item::before {
    content: attr(data-value);
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
    font-weight: 300;
    user-select: none;
    color: #fff;
    background-color: #c7cdde;
}
.thumbs {
    max-width: 700px;
    margin: 30px auto;
    padding: 0 100px;
    position: relative;
}
.thumbs .alice-carousel {
    position: relative;
    width: 100%;
    margin: auto;
    direction: ltr;
}
.thumbs .btn-prev {
    left: 25px;
}
.thumbs .btn-prev,
.thumbs .btn-next {
    position: absolute;
    top: 25px;
    font-size: 50px;
    color: #c7cdde;
    cursor: pointer;
}
.thumbs .btn-next {
    right: 25px;
}
.alice-carousel__wrapper {
    position: relative;
    overflow-x: hidden;
    overflow-y: hidden;
    box-sizing: border-box;
    width: 100%;
    height: auto;
}
.thumb,
.thumb > .item {
    width: 200px;
    height: 100px;
}
