.heroContainer {
    background-size: 100%;
    background-repeat: no-repeat;
    padding-top: 2%;
}

.heroContentContainer {
    display: flex;
    height: 100%;
    flex-direction: column;
    padding-top: 15%;
    line-height: 1.1;
    padding-left: 20%;
}

.textInteractive {
    width: 100%;
    color: #272343;
    font-size: 40px;
    font-weight: lighter;
    display: flex;
    justify-content: flex-start;
}

.textOnlineClasses {
    width: 100%;
    color: #2ebee1;
    font-size: 53px;
    font-weight: lighter;
}

.textSubHeading {
    width: 100%;
    color: #272343;
    font-size: 16px;
    font-weight: lighter;
    display: flex;
    justify-content: flex-start;
    margin-top: 20px;
}

.buttonGetStartedContainer {
    width: 100%;

    display: flex;
    justify-content: flex-start;
    margin-top: 32px;
}

.btnGetStarted {
    background-color: #ff8f44;
    width: 150px;
    padding-top: 8px;
    padding-bottom: 8px;
}

.component-container {
    overflow-x: hidden;
  }

.freeLessonHeader {
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fffef9;
    font-size: 28px;
    margin: 0px;
}
.headerText {
    font-family: 'Josefin Sans', sans-serif;
    color: 'red';
}
